import React from 'react';
import {Container, Row, Col} from 'reactstrap';
import {Link, ScrollElement} from 'react-scroll';

import arrowDown from '../images/arrow-drop-down.svg';
import './Header.css';

const Header = () => <header name="home">
  <Container className="h-100">
    <Row className="h-100">
      <Col className="my-auto text-center text-white">
        <h1>Hyperium</h1>
        <p className="lead">
          Hyperium is a Free and Open-source Minecraft Client with HUDs and other popular mods
        </p>
      </Col>
    </Row>
  </Container>
  <div className="scroll-down">
    <Link href="#features" to="features" smooth>
      <img src={arrowDown} alt="Scroll Down Arrow" width="50"/>
    </Link>
  </div>
</header>;

export default ScrollElement(Header);



