import React from 'react';
import {
  Container,
  Row,
  Col,
  Card,
  Button,
  CardTitle,
  CardText,
  CardColumns,
  CardBody
} from 'reactstrap';

import Section from './Section'

import './FeaturesSection.css';

class FeaturesSection extends React.Component {

  render() {
    return <Section id="features">
      <Container>
        <Row className="justify-content-center">
          <Col xs="8" sm="4" md="3" className="text-center">
            <h2>Features</h2>
            <hr/>
          </Col>
        </Row>
        <CardColumns>
          <Card>
            <CardBody>
              <CardTitle>Levelhead 4.1.2</CardTitle>
              <CardText>Levelhead is a Minecraft Mod created for the Hypixel Network mc.hypixel.net to show a player's network level above their head.</CardText>
              <Button href="https://www.youtube.com/watch?v=BqzTdH9llDc" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>ChromaHUD 3.0</CardTitle>
              <CardText>ChromaHUD is a Minecraft Mod to show custom information on your screen.</CardText>
              <Button href="https://www.youtube.com/watch?v=eyh6pcsGMpo" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>Autotip 2.0.3</CardTitle>
              <CardText>Autotip is a Minecraft Mod which automatically tips all active Network Boosters and other Autotip users on the Hypixel Network (hypixel.net), allowing the user to collect coins, karma, and experience (earnable currencies) over time.</CardText>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>KeystrokesMod 4.1.1</CardTitle>
              <CardText>Keystrokes is a Minecraft Mod to show the AWSD keys onscreen.</CardText>
              <Button href="https://www.youtube.com/watch?v=tA1SmI8nfY4" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>OldAnimations 1.0</CardTitle>
              <CardText>OldAnimations is a Minecraft Mod to turn back Minecraft 1.7 animations.</CardText>
              <Button href="https://www.youtube.com/watch?v=9-LoFff-3fI" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>AutoGG 2.0</CardTitle>
              <CardText>AutoGG is a Minecraft Mod which automatically says "gg" after a game has ended on Hypixel.</CardText>
              <Button href="https://www.youtube.com/watch?v=1eETPGuSQWA" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>AutoWho</CardTitle>
              <CardText>AutoWho is a Minecraft Mod which automatically run the "/who" command before a ranked game has started on Hypixel.</CardText>
              <Button href="https://www.youtube.com/watch?v=osJW53GA_1I" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>ToggleChat 1.0</CardTitle>
              <CardText>ToggleChat is a Minecraft Mod that allows you to view your character in at any angle by modifying camera placement.</CardText>
              <Button href="https://www.youtube.com/watch?v=7FdMMpzNdUk" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>PerspectiveMod 1.0</CardTitle>
              <CardText>PerspectiveMod is a Minecraft Mod that allows you to view your character in at any angle by modifying camera placement.</CardText>
              <Button href="https://www.youtube.com/watch?v=7FdMMpzNdUk" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
          <Card>
            <CardBody>
              <CardTitle>SidebarMod 1.0</CardTitle>
              <CardText>SidebarMod is a Minecraft Mod that allows you to edit the scoreboard placement and size</CardText>
              <Button href="https://www.youtube.com/watch?v=cn9VvT43yRs" target="_blank">Showcase video</Button>
            </CardBody>
          </Card>
        </CardColumns>
      </Container>
    </Section>;
  }

}

export default FeaturesSection;



